import React, { useEffect, useState } from "react";
import { motion, useViewportScroll, useTransform } from "framer-motion";
import face from "./face.png"
import Aos from "aos";
import "aos/dist/aos.css";

//Components
import ScrollForMore from "./scrollForMore";
import styled from 'styled-components'
import Typing from 'react-typing-animation';

const Container = styled.div`
    display:grid;
    grid-template-columns:1fr;
    
   

    
`;


const ColumnRight = styled.div`
  
    margin-top:100px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    background:white;
    align-items:flex-start;
    line-height:1.4;
    font-size:25px;
    padding:1rem 2rem;
    order:${({reverse})=>(reverse ? '1' : '2')};

    h1{
        margin-bottom:1rem;
        font-size : 22px;
    }

    p{
        max-width:100%;
        margin-bottom:2rem;
        font-size : 19px;
    }

`;


//Ease
const transition = { duration: 1, ease: [0.6, 0.01, -0.05, 0.9] };

const firstName = {
  initial: {
    y: 0,
  },
  animate: {
    y: 0,
    transition: {
      delayChildren: 0.6,
      staggerChildren: 0.04,
      staggerDirection: -1,
    },
  },
};

const lastName = {
  initial: {
    y: 0,
  },
  animate: {
    y: 0,
    transition: {
      delayChildren: 0.6,
      staggerChildren: 0.04,
      staggerDirection: 1,
    },
  },
};

const letter = {
  initial: {
    y: 400,
  },
  animate: {
    y: 0,
    transition: { duration: 1, ...transition },
  },
};

const ModelMobile3 = ({ imageDetails,artistData }) => {
  const { scrollYProgress } = useViewportScroll();
  const scale = useTransform(scrollYProgress, [0, 1], [1, 1.15]);

  const [canScroll, setCanScroll] = useState(false);

  useEffect(() => {
    Aos.init({duration:2000});
    if (canScroll === false) {
      document.querySelector("body").classList.add("no-scroll");
    } else {
      document.querySelector("body").classList.remove("no-scroll");
    }
  }, [canScroll]);

  return (
    <Container>
    <motion.div
      onAnimationComplete={() => setCanScroll(true)}
      className='single'
      initial='initial'
      animate='animate'
      exit='exit'>
      <div className='container fluid'>
        <div className='row center top-row'>
          <div className='top'>
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{
                opacity: 1,
                y: 0,
                transition: { delay: 1.2, ...transition },
              }}
              className='details'>
             
            </motion.div>
            <motion.div className='model'>
              <motion.span className='first' variants={firstName}>
                <motion.span variants={letter}>?</motion.span>
               
               
              </motion.span>
             
            </motion.div>
          </div>
        </div>
        <div className='row bottom-row'>
          <div className='bottom'>
            <motion.div className='image-container-single'>
              <motion.div
                initial={{
                  y: "-50%",
                  width: imageDetails.width,
                  height: imageDetails.height,
                }}
                animate={{
                  y: 0,
                  width: "100%",
                  height: window.innerWidth > 1440 ? 800 : 850,
                  transition: { delay: 0.2, ...transition },
                }}
                className='thumbnail-single'>
                <motion.div
                  className='frame-single'
                  whileHover='hover'
                  transition={transition}>
                  <motion.img
                    src={artistData.image}
                    alt='an image'
                    style={{ scale: scale }}
                    initial={{ scale: 1.0 }}
                    animate={{
                      transition: { delay: 0.2, ...transition },
                      y: window.innerWidth > 1440 ? -800 : -150,
                    }}
                  />
                </motion.div>
              </motion.div>
            </motion.div>
          </div>
          <ScrollForMore />
        </div>
      </div>
     
    </motion.div>

    <div className='detailed-information-mobile'>
      <ColumnRight  reverse ={true}>
                
        <h2>
        <Typing data-aos="fade-right"  > Kilka słów o mnie  </Typing>
        </h2>
        <p data-aos="fade-right" >{artistData.info}</p>

      </ColumnRight>
    </div>

               </Container>
  );
};

export default ModelMobile3;
