import styled,{css} from 'styled-components/macro';
import Button from '@material-ui/core/Button';
import {IoMdArrowRoundForward} from 'react-icons/io'
import {IoArrowForward,IoArrowBack} from 'react-icons/io5'

import logo from "../photos/main_logo.png"
import Aos from "aos";
import "aos/dist/aos.css"

const Conture1 = styled.img`
position:absolute;
left:200px;
    
`;

const Logo = styled.img`

    height:300px;
    width:300px;
 
    @media (max-width: 960px) {
        height:250px;
        width:250px;
       
    }
   

`;
const SliderButtons = styled.div`
    position:absolute;
    bottom:50px;
    right:50px;
    display:flex;
    z-index:10;
`;

const HeroSection = styled.section`
    height:100vh;

    position:relative;
    overflow:hidden;
`;

const Wrapper = styled.div`

width:100%;
height:100%;
display:flex;
justify-content:center;
align-items:center;
overflow:hidden;
position:relative;
`;

//1.27
const Slider = styled.div`
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    display:flex;
    align-items:center;
    justify-content:center;

    &::before{
        content:'';
        position:absolute;
        z-index:2;
        width:100%;
        height:100vh;
        bottom:0vh;
        left:0;
        overflow:hidden;
        opacity : 0.4;
        background:linear-gradient(
            0deg,
            rgba(0,0,0,0.2) 0%,
            rgba(0,0,0,0.2) 50%,
            rgba(0,0,0,0.6) 100%,
        );

    }

`;

const CustomImage = styled.img`
position:absolute;
top:0;
width:100vw;
height:100vh;
object-fit:cover;
    
`;
const Content = styled.div`
    position : absolute;
    z-index:10;
    display:flex;
    align-items:center;
    justify-content:'center';
  
    color:#fff;

    h1{
        //STYLES FOR STIDER CONTENT
        font-size:clamp(1rem,8vw,20rem);
        font-weight:400;
     
    }
    
`;

const SlideBox = styled.div`

    z-index:1;
    width:100%;
    height:100%;

    
`;

const Arrow = styled(IoMdArrowRoundForward)`
    
`;

const arrowButtons = css`
    width:50px;
    height:50px;
    color:#fff;
    cursor:pointer;
    background:#000d1a;
    border-radius:50px;
    padding:10px;
    margin-right:1rem;
    user-select:none;
    transition:0.3s;

    &:hover{
        background:#cd853f;
        transform:scale(1.02);

    }
`;

const PrevArrow = styled(IoArrowBack)`
    ${arrowButtons}
`;

const NextArrow = styled(IoArrowForward)`
    ${arrowButtons}
`; 
const Hero = ({slides}) => {

    const [current, setCurrent] = useState(0)
    const length = slides.length
    const timeout = useRef(null)

    useEffect(() => {
        const nextSlide=()=>{
            setCurrent(current=>(current===length-1? 0 : current + 1))

        }
        timeout.current = setTimeout(nextSlide,25000);
        return function (){
            if(timeout.current){
                clearTimeout(timeout.current);
            }
        }
    }, [current,length])

    const nextSlide=()=>{
        if(timeout.current){
            clearTimeout(timeout.current);
        }
        setCurrent(current === length - 1 ? 0 : current + 1)

    }
    const prevSlide=()=>{
        if(timeout.current){
            clearTimeout(timeout.current);
        }
        setCurrent(current === 0 ? length-1 : current - 1 )



    }

    return (
       
        <HeroSection>
            <Wrapper>
                {slides.map((slide,index)=>{
                    return(

                        <SlideBox key={index}>
                            {index ===current &&(

                            <Slider>
                            
                            <CustomImage src={slide.image} alt={slide.alt}/>
                           
                                <Content>
                                    <Logo src={logo}/>
                                  
                                    <Button href={slide.path}>
                                        {slide.label}

                                   
                                    </Button>
                                </Content>
                            
                            
                            </Slider>


                            )}



                        </SlideBox>
                    )

                })}

            <SliderButtons>
                <PrevArrow onClick={prevSlide}/>
                <NextArrow onClick={nextSlide}/>
            </SliderButtons>

            </Wrapper>
    
        </HeroSection>
    )
}

export default Hero;
