import React,{useEffect} from 'react'
import styled,{css} from 'styled-components/macro'
import {Link} from 'react-router-dom'
import { menuData } from '../data/MenuData';
import {FaBars} from 'react-icons/fa';
import {FaInstagram} from 'react-icons/fa'
import {FaFacebookSquare} from 'react-icons/fa'
import { IoLogoYoutube } from "react-icons/io";
import logo from "../photos/logo_cropped.png"
import Aos from "aos";
import "aos/dist/aos.css"

const Youtube = styled(IoLogoYoutube)`
    font-size:1.2rem;
    color:black;
    &:hover{
        color:red;
        animation: bounce 1s linear;
    }
    
`;
const Insta = styled(FaInstagram)`
    font-size:1.2rem;
    color:black;
    &:hover{
        color:purple;
        animation: bounce 1s linear;
    }
    
`;
const Facebook = styled(FaFacebookSquare)`
     font-size:1.2rem;
    color:black;
    &:hover{
        color:blue;
        animation: bounce 1s linear;
    }
    
`;


const MediaBar = styled.div`

display: flex;
margin: 1rem 0rem 1rem 0rem;
  justify-content: center;
  align-items: center;
  column-gap:20px;
    

    

    //@media screen and (max-width:768px){
    //    grid-template-columns:1fr;
    //    grid-template-rows :500px;
    //}
`;

const Nav = styled.nav`

height:80px;
display:flex;
box-shadow: 5px 5px black;

justify-content:space-between;
padding:1rem 1rem 1rem 5rem;
z-index:100;
position:fixed;
width:100%;
background:white;

@media screen and (max-width:960px){
    height:60px;
    display:flex;
    justify-content:space-between;
    padding:1rem 1rem 1rem 5rem;
    z-index:100;
    position:fixed;
    width:100%;
    background:black;
    box-shadow: 0px 0px black;
        
    }
`;
//rafce + tab

const NavLink = css`
    margin-right:1vw;
    color:black;
    font-size:13px;
    display:flex;
    align-items:center;
    padding:0 1rem;
    height:100%;
    cursor:pointer;
    text-decoration:none;

    &:hover{
        color:#000d1a;
        animation: bounce 1s linear;
    }

    


    @keyframes bounce{
        20%,50%,80%,to{
            transform: translateY(0);
        }
        40%{
            transform:translateY(-30px);
        }
        70%{
            transform:translateY(-15px);
        }
        90%{
            transform:translateY(-4px);
        }
    }
`;

const Logo = styled.img`
    height:60px;
    margin-left:5vw;
    @media screen and (max-width:960px){
        display:none;
        
    }

`;


const MenuBars = styled(FaBars)`
    display:none;
    color:black;
       margin-left:5vw;
    @media screen and (max-width:960px){
        color:white;
        display:block;
        background-size:contain;
        height:30px;
        width:30px;
        cursor:pointer;
        position:absolute;
        top:10px;
        right:0;
        transform: translate(-50%,25%);
    }

    
`;

const NavMenu = styled.div`
    display:flex;
    align-items:center; 
    color:black;
    margin-right:10%;
    margin-right:10%;

    @media screen and (max-width:960px){
        display:none;
    }
`;
const NavMenuLinks = styled(Link)`
    ${NavLink}

`;

const Navbar = ({handle}) => {

    useEffect(() => {
      
            window.scrollTo(0, 0)
         
        Aos.init({duration:2000});
     }, [])
 
    return (
        <Nav>
            <Logo src={logo}/>
            <p>page under construction 👨‍🏭</p>
            <MenuBars onClick={handle}/>
            <NavMenu>

                {menuData.map((item,index)=>(
                    <NavMenuLinks to={item.link} key={index}>
                        {item.title}
                    </NavMenuLinks>
                ))}
            <MediaBar> <Facebook/><Insta/><Youtube/></MediaBar>
            </NavMenu>
       
        </Nav>
    )
}

export default Navbar;
