import React,{useState} from 'react'
import Navbar from './components/Navbar'
import GlobalStyle from './globalStyles';
import Hero from './components/Hero'
import { SliderData } from './data/SilderData';
import NavbarMobile from "./components/NavbarMobile"
import InfoSectionRight from './components/InfoSectionRight';
import InfoSectionLeft from './components/InfoSectionLeft';
import { InfoData, InfoData2,InfoData3 } from './data/InfoData';
import RainbowScrollbar from 'react-rainbow-scrollbar';
import Footer from './components/Footer'
import Loader from './components/Loader'

import { HashRouter as Router, Switch, Route } from "react-router-dom";
import { AnimatePresence } from "framer-motion";

import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from '@material-ui/core';
import { useMediaQuery } from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import Contact from './components/Contact'

import image from './photos/scene_jck.jpg'

import {artistData} from './data/infoArtist1'
import {artistData2} from './data/infoArtist1'
import {artistData3} from './data/infoArtist1'
import "./components/Artist/App.scss";
import Model from './components/Artist/model';
import Model2 from './components/Artist/model2';
import Model3 from './components/Artist/model3';

import Home from './components/Artist/home'



import ModelMobile from './components/Artist/modelMobile'
import ModelSmall2 from './components/Artist/modelMobile2'
import ModelSmall3 from './components/Artist/modelMobile3'

import History from './components/History';
function App() {

  const [open,setOpen] = useState(false);
  const [load,setLoad] = useState(false);

  const imageDetails = {
    width: 250,
    height: 420,
  };

  const theme = useTheme();
  const isBigScreen = useMediaQuery(theme.breakpoints.up('md'));
  const handle = () =>{
    setOpen(!open)
  }

  function loader(){
    setTimeout(function(){ setLoad(true) }, 2000);
  }
  loader();
  return (
    <ThemeProvider theme={theme}>
    <div className="application">
      
      {!load &&
      <Loader/>
      }

      {load &&  
      <Router basename={process.env.PUBLIC_URL}>

              <Route
                exact
                path='/artist'
                render={() => 
                  <>
                  <GlobalStyle/>
                  <Navbar handle={handle} />
                  <NavbarMobile open = {open} handle={handle}/>
                  <RainbowScrollbar />
                  <Home imageDetails={imageDetails} />
                    <Footer/>
                </>
                }
              />
              <Route
                exact
                path='/history'
                render={() =>
                  <>
                  <GlobalStyle/>
                  <Navbar handle={handle} />
                  <NavbarMobile open = {open} handle={handle}/>
                  <RainbowScrollbar />
                  <History/>
                  <Footer/>
                  </> }
              />

              <Route
                exact
                path='/artist/1'
                render={() =>
                  <>
                  <GlobalStyle/>
                  <Navbar handle={handle} />
                  <NavbarMobile open = {open} handle={handle}/>
                 
                  { isBigScreen ? <Model imageDetails={imageDetails} artistData={artistData}/> :  <ModelMobile imageDetails={imageDetails} artistData={artistData}/> }
                  <Footer/>
                  </> }
              />

            <Route
                exact
                path='/artist/2'
                render={() =>
                  <>
                  <GlobalStyle/>
                  <Navbar handle={handle} />
                  <NavbarMobile open = {open} handle={handle}/>
                  <RainbowScrollbar />
                  { isBigScreen ? <Model imageDetails={imageDetails}  artistData={artistData2}/> :  <ModelSmall2 imageDetails={imageDetails}  artistData={artistData2}/> }
                  <Footer/>
                  </> }
              />

            <Route
                exact
                path='/artist/3'
                render={() =>
                  <>
                  <GlobalStyle/>
                  <Navbar handle={handle} />
                  <NavbarMobile open = {open} handle={handle}/>
                  <RainbowScrollbar />
                  { isBigScreen ? <Model imageDetails={imageDetails}  artistData={artistData3}/> :  <ModelSmall3 imageDetails={imageDetails} artistData={artistData3} /> }
                  <Footer/>
                  </> }
              />
              <Route
                exact
                path='/'
                render={() => <>
                  <GlobalStyle/>
                  <Navbar handle={handle} />
                  <NavbarMobile open = {open} handle={handle}/>
                  <RainbowScrollbar />
                  <Hero slides={SliderData} />
                  <InfoSectionLeft {...InfoData}/>
                  <InfoSectionLeft {...InfoData2}/>
                  <InfoSectionLeft {...InfoData3}/>
                  <Footer/>
                  </>}
              />
              <Route
                exact
                path='/contact'
                render={() => <>
                  <GlobalStyle/>
                  <Navbar handle={handle} />
                  <NavbarMobile open = {open} handle={handle}/>
                  <RainbowScrollbar />
                  <Contact image={image} />
                 
                  </>}
              />


   
    </Router>
    }
    </div>
    </ThemeProvider>
  );
}

export default App;

