import React,{useEffect} from 'react'
import styled from 'styled-components'
import {Link} from 'react-router-dom'
import {FaTimes} from 'react-icons/fa'
import {menuData} from '../data/MenuData'
import logo from "../photos/logo_cropped.png"
import Aos from "aos";
import "aos/dist/aos.css"
import {FaInstagram} from 'react-icons/fa'
import {FaFacebookSquare} from 'react-icons/fa'
import Bounce from 'react-reveal/Bounce';
import { IoLogoYoutube } from "react-icons/io";

const Youtube = styled(IoLogoYoutube)`
    font-size:1.8rem;
    color:white;
    &:hover{
        color:red;
        animation: bounce 1s linear;
    }
    
`;

const Insta = styled(FaInstagram)`
    font-size:1.8rem;
    &:hover{
        color:purple;
        animation: bounce 1s linear;
    }
   
    
`;
const Facebook = styled(FaFacebookSquare)`
     font-size:1.8rem;
     &:hover{
        color:blue;
        animation: bounce 1s linear;
    }
    
    
`;


const MediaBar = styled.div`

  display: flex;
  margin: 1rem 0rem 3rem 0rem;
  justify-content: center;
  align-items: center;
  column-gap:20px;
  color:white;
    
`;

const DropdownContainer = styled.div`
    height:100vh;
    padding-top:20%;
    padding-bottom:20%;
    position:fixed;
    z-index:999;
    width:100%;
    background:black;
    display:grid;
    align-items:center;
    top:0;
    left:0;
    transition:0.3s ease-in-out;
    opacity:${({open})=>(open ? '1' : '0')};
    top : ${({open})=>(open ? '0' : '100vh')};
`;

const Icon = styled.div`
    position:absolute;
    top:1.2rem;
    right:1.5rem;
    background:transparent;
    font-size:2rem;
    cursor:pointer;
    outline:none;

`;

const CloseIcon = styled(FaTimes)`
    color:white;
    font-size:30px;
    
`;

const DropdownWrapper = styled.div`

    
`;

const DropdownMenu = styled.div`
    
    display:grid;
    grid-template-columns:1fr;
    grid-template-rows:repeat(4,8rem);
    text-align:center;

    @media screen and (max-width: 480px){
        grid-template-rows: repeat(4,5rem);
    }
    

    
`;
 
const DropdownLink = styled(Link)`
    
    display:flex;
    row-gap:100px;
    align-items:center;
    justify-content:center;
    color:white;
    font-size:2rem;
    color:white;
    cursor:pointer;
    transition:0.2s ease-in-out;
    text-decoration:none;


    &:hover{
        color:red;
        animation: bounce 1s linear;
    }



    @keyframes bounce{
        20%,50%,80%,to{
            transform: translateY(0);
        }
        40%{
            transform:translateY(-30px);
        }
        70%{
            transform:translateY(-15px);
        }
        90%{
            transform:translateY(-4px);
        }
    }



`;
const NavbarMobile = ({open,handle}) => {

    useEffect(() => {
        window.scrollTo(0, 0);
        Aos.init({duration:2000});
     }, [])
 
    return (
        <DropdownContainer open={open} onClick={handle}>
                <Icon onClick={handle}>
                    <CloseIcon/>
                </Icon>


                <DropdownWrapper>
                    <DropdownMenu>
                    <MediaBar> <Insta/><Facebook/><Youtube/></MediaBar>
                        {menuData.map((item,index)=>(
                            <Bounce>
                                <DropdownLink  to={item.link} key={index}>
                                {item.title}
                                </DropdownLink>
                            </Bounce>
                            
                        ))}
                       
                    </DropdownMenu>
                    
                </DropdownWrapper>
                
        </DropdownContainer>
    )
}

export default NavbarMobile
